/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 1.8rem;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

h1,
h2,
h3 {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

.auth {
  width: min(95%, 60rem);
  margin: 0 auto;
}

.formulario {
  display: flex;
  flex-direction: column;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1.5rem 0;
}
.formulario__Label {
  display: block;
  font-size: 1.8rem;
  color: rgba(26, 27, 21, 0.8);
}
.formulario__input {
  padding: 1rem;
  border: 1px solid #b1c7dd;
  border-radius: 0.5rem;
}
.formulario__submit {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
}
.formulario__submit:hover {
  background-color: #0063c1;
  cursor: pointer;
}
.formulario__submit {
  width: auto;
  align-self: flex-start;
}

.acciones {
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
}
.acciones__enlace {
  color: #1a1b15;
  font-size: 1.6rem;
  font-weight: 300;
}

.header {
  min-height: 60rem;
  background-image: url("../img/grafico.svg"), url("../img/grafico.svg"), linear-gradient(to right, #1a1b15, rgba(26, 27, 21, 0.5)), image-set(url("../img/header.avif") 1x, url("../img/header.webp") 1x, url("../img/header.jpg") 1x);
  background-repeat: no-repeat;
  background-size: 400px, 400px, cover, cover;
  background-position: top left, bottom right;
}
.header__contenedor {
  width: min(95%, 140rem);
  margin: 0 auto;
}
.header__navegacion {
  padding: 5rem 0;
  display: flex;
  justify-content: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .header__navegacion {
    justify-content: flex-end;
  }
}
.header__enlace {
  color: #ffffff;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: color 0.3s ease, transform 0.3s ease;
}
.header__enlace:hover {
  transform: scale(1.1);
  color: #007df4;
}
.header__contenido {
  padding-top: 10rem;
}
@media (min-width: 768px) {
  .header__contenido {
    padding-top: 5rem;
  }
}
.header__logo {
  font-size: clamp(1rem, -0.8rem + 10vw, 8rem);
  background: linear-gradient(to right, #007df4, #00c8c2);
  background-clip: text;
  color: transparent;
  text-align: center;
}
@media (min-width: 768px) {
  .header__logo {
    text-align: left;
    display: inline-block;
  }
}
.header__texto {
  font-size: 3rem;
  font-weight: 700;
  color: #ffffff;
  margin: 2rem 0;
  text-align: center;
}
@media (min-width: 768px) {
  .header__texto {
    text-align: left;
    text-transform: uppercase;
  }
}
.header__texto--modalidad {
  font-size: 2rem;
}
.header__boton {
  background-color: #00c8c2;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
}
.header__boton:hover {
  background-color: #009591;
  cursor: pointer;
}
.header__boton {
  text-transform: uppercase;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .header__boton {
    width: auto;
  }
}

.barra {
  background-color: #007df4;
}
.barra__contenido {
  width: min(95%, 140rem);
  margin: 0 auto;
}
.barra__contenido {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .barra__contenido {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.barra__logo {
  color: #ffffff;
  text-align: center;
  margin-bottom: 2rem;
}
@media (min-width: 1024px) {
  .barra__logo {
    text-align: left;
    margin: 0;
  }
}
.barra__logo {
  font-size: clamp(2rem, -0.8rem + 10vw, 3rem);
}

.navegacion {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .navegacion {
    flex-direction: row;
  }
}
.navegacion__enlace {
  color: #ffffff;
  font-size: 2.2rem;
  padding: 2rem;
  transition: background-color 0.3s, color 0.3s;
  font-weight: 900;
  text-transform: uppercase;
  text-align: center;
}
.navegacion__enlace:hover {
  background-color: #ffffff;
  color: #007df4;
}